/**
  This is the entrypoint less file that's loaded onto index.html
*/
/* stylelint-disable */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  background: #ffdada;
  box-sizing: border-box;
  font-family: 'Helvetica', 'Arial', sans-serif;
  transition: background 1s;
}
body.about_active {
  background: #ffdada;
}
body.work_active {
  background: #da9ff9;
}
body.dnd_active {
  background: #98acf8;
}
.main_container {
  padding: 5rem 1rem;
}
@media all and (max-width: 479px) {
  .main_container {
    padding: 2rem 1rem;
  }
}
@keyframes noop {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes front_card_load {
  0% {
    opacity: 0;
    transform: translateX(0) translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateX(-1.5rem) translateY(-1.5rem);
  }
}
@keyframes front_card_loop {
  0% {
    opacity: 1;
    transform: translateX(-1.5rem) translateY(-1.5rem);
  }
  50% {
    opacity: 0.5;
    transform: translateX(0) translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateX(-1.5rem) translateY(-1.5rem);
  }
}
@keyframes back_card_load {
  0% {
    opacity: 0;
    transform: translateX(0) translateY(0);
  }
  100% {
    opacity: 0.5;
    transform: translateX(1.5rem) translateY(1.5rem);
  }
}
@keyframes back_card_loop {
  0% {
    transform: translateX(1.5rem) translateY(1.5rem);
  }
  50% {
    transform: translateX(0) translateY(0);
  }
  100% {
    transform: translateX(1.5rem) translateY(1.5rem);
  }
}
.card_stack {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  margin: 0 auto;
  position: relative;
}
@media all and (max-width: 479px) {
  .card_stack {
    width: 16rem;
  }
}
.front_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  background: transparent;
  border: 2px solid white;
  pointer-events: none;
  transform: translateX(-1.5rem) translateY(-1.5rem);
  z-index: 2;
}
@media all and (max-width: 479px) {
  .front_card {
    width: 16rem;
  }
}
.secret_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  overflow: hidden;
  pointer-events: none;
  z-index: 3;
}
@media all and (max-width: 479px) {
  .secret_card {
    width: 16rem;
  }
}
.secret_card .secret_border_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  border: 2px solid #ffdada;
  transform: translateX(-1.5rem) translateY(-1.5rem);
  transition: border 1s;
}
@media all and (max-width: 479px) {
  .secret_card .secret_border_card {
    width: 16rem;
  }
}
.content_card {
  height: 36rem;
  position: absolute;
  width: 20rem;
  background: white;
  border-radius: 1rem;
  box-shadow: 0px 1px 2px 1px #DDDDDD;
  padding: 2rem;
  padding-right: 3.5rem;
  z-index: 1;
}
@media all and (max-width: 479px) {
  .content_card {
    width: 16rem;
  }
}
.back_card {
  border-radius: 1rem;
  height: 36rem;
  position: absolute;
  width: 20rem;
  background: white;
  opacity: 0.5;
  transform: translateX(1.5rem) translateY(1.5rem);
  z-index: 0;
}
@media all and (max-width: 479px) {
  .back_card {
    width: 16rem;
  }
}
.initial .front_card {
  animation: front_card_load 1s;
  animation-fill-mode: forwards;
}
.initial .secret_card .secret_border_card {
  animation: front_card_load 1s;
  animation-fill-mode: forwards;
}
.initial .back_card {
  animation: back_card_load 1s;
  animation-fill-mode: forwards;
}
.loop .front_card {
  animation: front_card_loop 1s;
  animation-fill-mode: forwards;
}
.loop .secret_card .secret_border_card {
  animation: front_card_loop 1s;
  animation-fill-mode: forwards;
}
.loop .back_card {
  animation: back_card_loop 1s;
  animation-fill-mode: forwards;
}
body.about_active .secret_card .secret_border_card {
  border: 2px solid #ffdada;
}
body.work_active .secret_card .secret_border_card {
  border: 2px solid #da9ff9;
}
body.dnd_active .secret_card .secret_border_card {
  border: 2px solid #98acf8;
}
.content {
  transform: translateY(0);
}
.content h1,
.content h2 {
  color: #ff7474;
  display: inline-block;
  font-weight: 700;
  font-size: 1.4rem;
  text-align: center;
}
.content hr {
  border-top: 2px solid #ff7474;
}
.content .blurb {
  color: #494e51;
  line-height: 1.5;
  margin-bottom: 1rem;
}
.content .question {
  color: #ff7474;
  font-weight: 700;
  line-height: 1.5;
}
.content a {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  /* Remove excess padding and border in Firefox 4+ */
  /* my own styles */
  cursor: pointer;
  outline: 0;
  text-decoration: none;
  color: #ff7474;
}
.content a::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.content a.active,
.content a:focus,
.content a:hover {
  color: #ff0e0e;
}
.content .img_container {
  text-align: center;
  margin-bottom: 0.5rem;
}
.content .section .info {
  margin-bottom: 2rem;
}
.content .section:not(.active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
@media all and (max-width: 479px) {
  .content {
    font-size: 0.8rem;
  }
  .content h1,
  .content h2 {
    font-size: 1rem;
  }
}
.about.section h1,
.about.section h2 {
  color: #ff7474;
}
.about.section hr {
  border-top: 2px solid #ff7474;
}
.about.section .portrait {
  border-radius: 0.5rem;
  border: 2px solid #ff7474;
  height: 6rem;
  width: 6rem;
}
.work.section h2 {
  color: #b53ff3;
}
.work.section hr {
  border-top: 2px solid #b53ff3;
}
.work.section a {
  color: #b53ff3;
}
.work.section a.active,
.work.section a:focus,
.work.section a:hover {
  color: #820cc0;
}
.work.section .question {
  color: #b53ff3;
}
.work.section .ziprecruiter {
  border-radius: 0.5rem;
  width: 90%;
}
.dnd.section h2 {
  color: #385ff2;
}
.dnd.section hr {
  border-top: 2px solid #385ff2;
}
.dnd.section .dnd_logo {
  height: 6rem;
}
.dnd.section a {
  color: #385ff2;
}
.dnd.section a.active,
.dnd.section a:focus,
.dnd.section a:hover {
  color: #0c30b8;
}
.dnd.section .question {
  color: #385ff2;
}
@keyframes content_load {
  0% {
    opacity: 0;
    transform: translateY(1.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes content_loop {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  25% {
    opacity: 0;
    transform: translateY(0);
  }
  50% {
    opacity: 0;
    transform: translateY(1.5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.initial .content {
  animation: content_load 1s;
}
.loop .content {
  animation: content_loop 1s;
}
.menu {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.tab_button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  /* Remove excess padding and border in Firefox 4+ */
  /* my own styles */
  cursor: pointer;
  outline: 0;
  text-decoration: none;
  border: 2px solid transparent;
  border-radius: 0.5rem;
  color: #494e51;
  padding: 0.5rem;
}
.tab_button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.tab_button.active,
.tab_button:focus,
.tab_button:hover {
  border: 2px solid #ffdada;
}
.tab_button.about_button.active,
.tab_button.about_button:focus,
.tab_button.about_button:hover {
  color: #ff7474;
  border: 2px solid #ff7474;
}
.tab_button.work_button.active,
.tab_button.work_button:focus,
.tab_button.work_button:hover {
  color: #b53ff3;
  border: 2px solid #b53ff3;
}
.tab_button.dnd_button.active,
.tab_button.dnd_button:focus,
.tab_button.dnd_button:hover {
  color: #385ff2;
  border: 2px solid #385ff2;
}
